import { Component } from '@angular/core';
import '../../public/css/styles.css';

import { Hero } from './Hero';
import { Biller } from './Biller';

//default
const HEROES = ["", "", "", ""];

@Component({
  selector: 'my-app',
  template: require('./app.component.html'),
  styles: [require('./app.component.css')]
})
export class AppComponent {
  heroes: any[] = [];
  heroName: string;
  billForm: any;
  result: string;

  constructor() {
    for (let i = 0; i < HEROES.length; i++) {
        this.heroes.push(new Hero(HEROES[i]));
    }
  }


  addHero() {
    if (this.heroName === "") {
        alert("enter name for our hero!");
        return;
    }
    this.heroes.push(new Hero(this.heroName));
  }

  deleteHero(i: number) {
    this.heroes.splice(i, 1);
  }

  calc(formElmt:any) {
    if (!formElmt.form.valid) {
        this.result = "What are u doing"
        window.location.href = window.location.href + "#result";
        return;
    }

    let neg = new Biller().bill(this.heroes);
    let r = "";
    if (neg.length === 0) {
        console.log("yay!!");
        r += "Yay, "
    } else {
        console.log("shit!");
        for (let l = 0; l < neg.length; l++) {
            r += neg[l].name + " should pay\n" + neg[l].to + "\n\n";
            console.log(neg[l].name + " shoud pay: " + neg[l].to);
        }
    }
    this.result = r;
    window.location.href = window.location.href + "#result";
  }


}



